/* 1px hack */
.ui-border,
.ui-border-radius,
.ui-border-t,
.ui-border-l,
.ui-border-b,
.ui-border-r,
.ui-border-tb {
  position: relative; }

.ui-border-b:before {
  border-bottom: 1px solid #dcdcdc;
  content: '';
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  -webkit-transform-origin: left bottom; }
  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    .ui-border-b:before {
      -webkit-transform: scaleY(0.5); } }
  @media screen and (-webkit-min-device-pixel-ratio: 3) {
    .ui-border-b:before {
      -webkit-transform: scaleY(0.3333); } }

.ui-border-t:before {
  border-top: 1px solid #dcdcdc;
  content: '';
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-transform-origin: left top; }
  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    .ui-border-t:before {
      -webkit-transform: scaleY(0.5); } }
  @media screen and (-webkit-min-device-pixel-ratio: 3) {
    .ui-border-t:before {
      -webkit-transform: scaleY(0.3333); } }

.ui-border-l:before {
  border-left: 1px solid #dcdcdc;
  content: '';
  display: block;
  bottom: 0;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-transform-origin: left top; }
  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    .ui-border-l:before {
      -webkit-transform: scaleX(0.5); } }
  @media screen and (-webkit-min-device-pixel-ratio: 3) {
    .ui-border-l:before {
      -webkit-transform: scaleX(0.3333); } }

.ui-border-r:before {
  border-right: 1px solid #dcdcdc;
  content: '';
  display: block;
  bottom: 0;
  position: absolute;
  right: 0;
  top: 0;
  -webkit-transform-origin: right top; }
  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    .ui-border-r:before {
      -webkit-transform: scaleX(0.5); } }
  @media screen and (-webkit-min-device-pixel-ratio: 3) {
    .ui-border-r:before {
      -webkit-transform: scaleX(0.3333); } }

.ui-border-tb:before {
  border-top: 1px solid #dcdcdc;
  content: '';
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-transform-origin: left top; }
  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    .ui-border-tb:before {
      -webkit-transform: scaleY(0.5); } }
  @media screen and (-webkit-min-device-pixel-ratio: 3) {
    .ui-border-tb:before {
      -webkit-transform: scaleY(0.3333); } }

.ui-border-tb:after {
  border-bottom: 1px solid #dcdcdc;
  content: '';
  display: block;
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0;
  -webkit-transform-origin: left bottom; }
  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    .ui-border-tb:after {
      -webkit-transform: scaleY(0.5); } }
  @media screen and (-webkit-min-device-pixel-ratio: 3) {
    .ui-border-tb:after {
      -webkit-transform: scaleY(0.3333); } }

.ui-border:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #dcdcdc;
  -webkit-transform-origin: 0 0;
  padding: 1px;
  -webkit-box-sizing: border-box;
  pointer-events: none;
  z-index: 100;
  pointer-events: none; }
  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    .ui-border:before {
      width: 200%;
      height: 200%;
      -webkit-transform: scale(0.5); } }
  @media screen and (-webkit-min-device-pixel-ratio: 3) {
    .ui-border:before {
      width: 300%;
      height: 300%;
      -webkit-transform: scale(0.3333); } }

.ui-border-radius:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #dcdcdc;
  -webkit-transform-origin: 0 0;
  padding: 1px;
  -webkit-box-sizing: border-box;
  border-radius: 0.05rem;
  pointer-events: none;
  z-index: 100; }
  @media screen and (-webkit-min-device-pixel-ratio: 2) {
    .ui-border-radius:before {
      width: 200%;
      height: 200%;
      -webkit-transform: scale(0.5);
      border-radius: 0.1rem; } }
  @media screen and (-webkit-min-device-pixel-ratio: 3) {
    .ui-border-radius:before {
      width: 300%;
      height: 300%;
      -webkit-transform: scale(0.3333);
      border-radius: 0.15rem; } }

.ui-list > li.ui-border-t:first-child:before,
.ui-list > li:first-child .ui-border-t:before,
.ui-list-active > li:active:before,
.ui-list > li.active:before,
.ui-list > li.active .ui-border-t:before,
.ui-list > li.active + li .ui-border-t:before,
.ui-list > li.active + li.ui-border-t:before {
  content: none; }
